<template>
    <div v-if="props.list?.length" class="echart-dom" ref="echartDom" ></div>
</template>

<script setup >
    import { ref, watch, onBeforeUnmount } from "vue";
    import * as Echarts from "echarts";
    import { useLineEchart } from "../utils/hooks";
    let props = defineProps({
        list:Array,
        desc: Array
    })
    let echartDom = ref(null);
    let myEchart;
    watch(() => [props, echartDom.value], () => {
        console.log("props->lineEchart", props)
        if(echartDom.value) {
            let myEchart = Echarts.init(echartDom.value)
            myEchart?.setOption(useLineEchart(props.list, props.desc), true)
        }
    }, {deep: true})
    onBeforeUnmount(() => {
        if (myEchart) {
            myEchart?.dispose()
            myEchart = null;
           echartDom.value?.setAttribute('_echarts_instance_', '');
        }
    })
</script>

<style scoped >
    .echart-dom{
        width: 100%;
        height: 220px;
        background-color: #fff;
        padding: 20px 0;
        border-radius: 5px;
    }
</style>