<template>
    <div class="card-data" >
        <div class="card-data-title" >{{props.desc.title}}</div>
        <CellGroup>
            <Cell 
                v-for="(item) in enumDesc[props.desc.type ?? '7']?.flat(Infinity)" 
                :title="item.label" 
                :key="item.key"
                :value="fil(data, item)"
             ></Cell>
        </CellGroup>
        <div>
            <div class="proposal-item" v-for="(item) in proposal" >
                <span>{{item.name}}：</span>
                <span style="color: #666;" >{{data?.[desc.key]?.[item.key] || "暂无数据"}}</span>
            </div>
        </div>
         <LineData :type="props.desc.type" />
    </div>
</template>

<script setup >
import { defineProps, onMounted, ref } from "vue";
import { Cell, CellGroup } from "vant"
import { enumDesc } from "../../../../utils/enum";
import { proposal } from "./enum";
import LineData from "./lineData.vue"
let props = defineProps({
    type: {
        type: String,
        defaultValue: 9
    },
    data: Object,
    desc: Object
})
let type = ref("");
let fil = (data, item) => {
    let enumJson = {
        1: "空腹",
        2: "餐前", 
        3: "餐后",
        4: "随机"
    }
    if(item.key === "bloodsugar_type"){
        return enumJson[data?.[item.key]] ?? "暂无数据"
    }else{
        return data?.[item.key] ? data?.[item.key] + item.unit : '暂无数据'
    }
}
onMounted(() => {
    type.value = props.type 
    console.log("desc", props.desc.type)
})
</script>

