<template>
    <van-nav-bar
        title="报告"
        fixed="true"
        left-arrow
        @click-left="back"
    />
    <div class="report-inner" >
        <SubTitle isShowTime title="检测项目" :time="time" >
            <Tabel :data="data" :column="column" />
        </SubTitle>
        <SubTitle title="数据解读" >
            <CardData v-for="(item) in cardDesc" :desc="item" :data="reportData" />
        </SubTitle>
    </div>
</template>

<script setup >
    import {onBeforeMount, ref} from "vue"
    import { useRouter } from "vue-router";
    import axios from "../../../utils/axios"
    import { getIDCardNo } from "../common/util";
    import { DateUtil } from "../common/util/dateUtil";
    import { column, cardDesc } from "./component/enum";
    import SubTitle from "./component/subTitle.vue"
    import Tabel from "./component/tabel.vue"
    import CardData from "./component/cardData.vue";
    import moment from "moment";
    let { back, push } = useRouter();
    let data = ref([
        {
            label: '收缩压',
            key: "systolic",
            unit: 'mmHg',
            range: "90～120mmHg"
        },
        {
            label: '舒张压',
            key: "diastolic",
            unit: 'mmHg',
            range: "60～80mmHg"
        },
        {
            label: '血氧饱和度',
            key: "bloodoxygen",
            unit: '%',
            range: ">94%"
        },
        {
            label: '脉搏',
            key: "pulse",
            unit: '次/分',
        },
        {
            label: '身高（cm）',
            key: "height",
            unit: 'cm',
        },
        {
            label: '体重（kg）',
            key: "weight",
            value: 0,
            unit: 'kg',
        },
        {
            label: '血糖',
            key: "bloodsugar",
            unit: '%',
        },
        {
            label: '尿酸',
            key: "uricacid",
            unit: '%',
        },
        {
            label: '总胆固醇',
            value: '0',
            key: "chol",
            unit: 'mmol/L',
            range: "TC＜5.2"
        },
        {
            label: '血红蛋白',
            value: 0,
            key: "hb",
            unit: 'g/L',
            range: "成年男性：12.0～16.0g/dL 成年女性：11.0～15.0g/dL"
        }
    ])
    let reportData = ref();
    let time = ref()
    let getHistoryReport = async () => {
        try {
            let bloodsugarJson = {
                1: "3.9～6.1（空腹）",
                2: "3.9～6.1 （餐前）",
                3: "3.9～7.8（餐后）",
                4: "3.9～11.0（随机）"
            }
            let params = {
                idcard: getIDCardNo(),
                startDate: DateUtil.getStartDayOfYear(), 
                endDate: DateUtil.getEndDayOfYear()
            }
            let res = await axios.get("/report/history/list", {params})
            console.log("【获取历史报告数据】：", JSON.parse(res.data[0].reportInfo))
            let reportInfo = JSON.parse(res.data[0].reportInfo);
            time.value = moment(res.data[0].reportDate).format("YYYY-MM-DD HH:mm:ss");
            data.value = data.value.map(item => {
                if(item.key === "bloodsugar"){
                    console.log("item.bloodsugar_type", item.bloodsugar_type)
                    return {...item, value: reportInfo[item.key] ? reportInfo[item.key] + item.unit : "", range: bloodsugarJson[reportInfo.bloodsugar_type ?? "1"]}
                }
                return {...item, value: reportInfo[item.key] ? reportInfo[item.key] + item.unit : ""}
            })
            console.log("data.value", data.value)
            reportData.value = reportInfo
        } catch (error) {
            console.error("【获取历史报告失败】：", error)
        }
    }
    onBeforeMount(getHistoryReport)
</script>

<style scoped >
    .report-inner{
        padding: 0 10px;
        padding-top: 16px;
    }
</style>