<template>
    <div>
        <div class="line-data-title" >
            <div>历史情况</div>
            <div class="toggle" >
                <div @click="active = '1'" :class="[active === '1' ? 'active' : '']" >周</div>
                <div @click="active = '2'" :class="[active === '2' ? 'active' : '']" >月</div>
            </div>
        </div>
        <div>
            <LineEchart v-if="enumDesc[props.type]?.flat(Infinity).every(item => item.key != 'heartRate')" v-for="(item) in enumDesc[props.type]" :list="data" :desc="fil(item)" />
            <ItemImage v-else :historyList="data" :desc="item" />
        </div>
    </div>
</template>

<script setup >
import { defineProps, ref, watchEffect, onMounted } from "vue";
import moment from 'moment'
import { useDate } from '../../../../utils/hooks'
import LineEchart from "@/components/lineEchart.vue";
import { enumDesc } from "../../../../utils/enum";
import { getIDCardNo } from "../../common/util";
import axios from "../../../../utils/axios"
import ItemImage from "@/components/itemImage.vue"
let props = defineProps({
    type: String
})
let { getWeekData, getMonthData } = useDate()
let data = ref([]);
let active = ref("1")
onMounted(() => {
    console.log("props.type", props.type)
})
let getHistoryReport = async () => {
    try {
        let params = {
            idcard: getIDCardNo(),
            startDate: moment(active.value === "1" ? getWeekData(1) : getMonthData(1)).format("YYYY-MM-DD"), 
            endDate: moment(+new Date()).format("YYYY-MM-DD")
        }
        let res = await axios.get("/report/history/list", {params})
        console.log("【获取历史报告数据】：", JSON.parse(res.data[0].reportInfo))
        data.value = res.data.map(item => ({...JSON.parse(item.reportInfo), startTime: moment(item.reportDate).format("YYYY-MM-DD HH:mm:ss")}))
        console.log(" data.value",  data.value)
    } catch (error) {
        console.error("【获取历史报告失败】：", error)
    }
}
let fil = (list) => list?.filter(it => !it.isHide) ?? list
watchEffect(() => {
    getHistoryReport()
})
</script>

<style scoped lang="less" >
    .line-data-title{
        height: 40px;
        line-height: 40px;
        padding: 0 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .toggle{
            display: flex;
            border: 1px solid #06B48D;
            border-radius: 5px;
            div{
                width: 34px;
                height: 22px;
                text-align: center;
                line-height: 23px;
                &:last-child{
                    border-left: 1px solid #06B48D;
                }
            }
            .active{
                background-color: #06B48D;
                color: #fff;
            }
    }
    }
</style>